.home{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 100%;
}
.home-room-list,.home-room-member{
    width: 30%;
    /*border: 1px solid #f00;*/
}
.home-room-chatting{
    width: 100%;
    height: 100%;
}
.home-room-chatting-list{
}
.home-room-chatting-current{
    height: 100%;
}

.chat-room {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 5px;
    width: 100%;
    height: 100%;
    max-width: 1000px;
    margin: auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
.tit {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    text-align: center;
    vertical-align: middle;
    border: 1px solid #000;
    border-radius: 5px;
    font-size: 30px;
    font-weight: bold;
    height: 60px;
    line-height: 60px;
}
.content {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin-top: 10px;
    border-radius: 2px;
    border: 1px solid #000;
    height: 100%;
    padding: 20px;
    overflow: auto;
}
.tool {
    text-align: center;
    margin-top: 10px;
    padding: 5px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    border: 1px solid #000;
    border-radius: 5px;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.write-board {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 100%;
    padding: 10px;
}
.write-box {
    /*text-indent: 20px;*/
    border: none;
    width: 100%;
    /*padding: 10px;*/
    white-space: normal;
    word-wrap: break-word;
    word-break: break-all;
}
.send {
    display: inline-block;
    padding: 0 5px;
    white-space: nowrap;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    color: #000;
}
.send:hover {
    color: #0af;
}
.chat-line {
    margin-top: 20px;
    overflow: hidden;
}
.chat-man {
    float: left;
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    width: 35px;
    height: 35px;
    margin: 7px 10px 5px 0;
    border-radius: 50%;
    font-weight: bold;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    font-size: 12px;
    border: 3px solid #aaa;
    background: #000;
    color: #fff;
}
.chat-moment {
    font-size: 10px;
    display: block;
    color: #777;
}
.chat-member {
    font-size: 14px;
    font-weight: bold;
    color: #777;
}
.chat-mess {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: inline-block;
    max-width: 80%;
    overflow: hidden;
    border: 1px solid #ccc;
    padding: 6px;
    border-radius: 3px;
    background: rgba(100, 100, 100, 0.8);
    color: #fff;
    white-space: normal;
    word-wrap: break-word;
    word-break: break-all;
}
.chat-moment-me {
    text-align: right;
}
.chat-man-me {
    background: #eee;
    color: #000;
    border-color: #aaa;
    float: right;
    margin: 7px 0 5px 10px;
}
.chat-mess-me {
    float: right;
}
.chat-in {
    display: block;
    text-align: center;
    font-size: 10px;
    color: rgb(0, 100, 255);
}
.chat-out {
    display: block;
    text-align: center;
    font-size: 10px;
    color: rgb(255, 100, 0);
}

.room-list{
    padding: 10px 20px;
    color: #777;
}
.room-list-item{
    padding-top: 10px;
    cursor: pointer;
}
.room-list .active,.room-list-item:hover .room-list-item-content{
    color: #000;
    font-weight: bold;
    text-shadow: 1px 0 1px rgba(0, 0, 0, 1);
    border-color: transparent;
}
.room-list-item-content{
    border-bottom: 1px solid #eee;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
}

.tool-part {
    padding-top: 3px;
    margin: 10px 20px;
    color: #000;
    border-top: 3px solid rgba(0,0,0,0.7);
    border-radius: 3px 3px 0 0;
    background: rgba(238,238,238,0.5);
    position: relative;
}
.tool-part .active {
    font-weight: bold;
    text-shadow: 1px 0 1px rgba(0, 0, 0, 1);
}
.tool-part-list {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.tool-part-list-item {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    text-align: center;
    width: 50%;
}
.tool-part-list-item>span{
    cursor: pointer;
}
.tool-part-form {
    margin: 0 20px;
    overflow: hidden;
    margin-bottom: 8px;
}
.tool-part-input {
    width: 100%;
    border: none;
    border-bottom: 1px solid #000;
    background: transparent;
    font-weight: bold;
}
.shrink-enter-active, .shrink-leave-active {
    -webkit-transition: width .5s;
    transition: width .5s;
}
.shrink-enter, .shrink-leave-to {
    width: 0;
}
.tool-part-form-box{
    position: absolute;
    width: 100%;
    background: rgba(238,238,238,0.5);
}
.tool-part-form-confirm{
    text-align: center;
}
.tool-part-confirm-btn {
    cursor: pointer;
    text-align: center;
}
.tool-part-confirm-btn:hover {
    font-weight: bold;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 1);
}
.tool-part-result:before{
    content:"";
    display: inline-block;
    border: 6px solid rgba(0,0,0,0.7);
    border-top-color: transparent;
    border-left-color: transparent;
    border-right-color: transparent;
    margin-top: -12px;
    vertical-align: top;
}
.tool-part-result{
    color: #fff;
    padding: 0 16px;
    text-align: center;
    background: rgba(0,0,0,0.7);;
    /*border: 1px solid rgba(0,0,0,0.7);*/
    font-size: 0;
    vertical-align: top;
    border-radius: 0 0 5px 5px;
    min-height: 4px;
}
.tool-part-result-item{
    font-size: 16px;
    cursor: pointer;
    /*text-decoration: underline;*/
    padding-bottom: 10px;
    position: relative;
}
.tool-part-result-item:hover,.result-active{
    font-weight: bold;
    text-shadow: 1px 1px 1px rgba(255, 255, 255, 1);
}
.tool-part-result-form>*{
    background: transparent;
    border:none;
    margin: 15px 0;
}
.tool-part-result-input{
    border-bottom: 1px solid #fff;
    color: #fff;
}
.tool-part-result-input::-webkit-input-placeholder{
    color: #aaa;
}
.tool-part-result-input:-ms-input-placeholder{
    color: #aaa;
}
.tool-part-result-input::-ms-input-placeholder{
    color: #aaa;
}
.tool-part-result-input::placeholder{
    color: #aaa;
}
.tool-part-result-btn{
    display: inline-block;
    padding: 0 10px;
    border: 1px solid #fff;
    font-size: 16px;
    cursor: pointer;
    border-radius: 4px 4px 4px 0;
}
.tool-part-result-btn:hover{
    background: #fff;
    color: #000;
}

